<template lang='pug'>
v-app
  v-navigation-drawer(id='nav' app floating)
    v-container(fill-height)
      img(id='me' src='./assets/me.png' to='/')
      v-flex(sm12) 
        h1 {{name}} <br/> {{surname}}
        ol(v-for='item in pages') 
          router-link(:to='item.link') {{item.name}}
  v-container
    v-flex(hidden-lg-and-up id='smallname' wrap) 
        h1 {{name}} {{surname}} 
    v-flex(hidden-lg-and-up id='smalllinks' wrap) 
      span(v-for='(item, i) in pages') 
        router-link(:to='item.link') {{item.name}}
  v-content(hidden-lg-and-up)
    router-view
</template>

<script>
export default {
  data () {
    return {
      pages: [
        { name: 'about', link: '/'},
        { name: 'projects', link: '/projects'},
        { name: 'thoughts', link: '/thoughts'},
      ],
      name: 'kieran',
      surname: 'cutting'
    }
  },
  name: 'App'
}
</script>

<style lang="sass">
$scarlet-dark: #BD0202;
$grey: #F4F3F1;

#me
  position: absolute;
  width: 150px;
  top: 150px;
  right: 0px;
#app 
  background-color: $grey;
  font-family: 'Josefin-Sans', sans-serif;
#smallname
  text-align: center;
  h1
    font-size: 40px;
#smalllinks
  text-align: center;
  span
    padding: 0px 4% 0px 4%;
  a
    color: $scarlet-dark;
    font-weight: lighter;
    font-size: 20px;
    text-decoration: none;
  a:hover
    text-decoration: underline;
.container
    flex: none;
#nav
  background-color: $grey;
  h1 
    font-size: 40px;
    text-align: right;
    font-family: 'Acme', sans-serif;
  ol
    text-align: right;
  a
    color: $scarlet-dark;
    font-weight: lighter;
    font-size: 20px;
    text-decoration: none;
  a:hover
    text-decoration: underline;
</style>



